.newItem {
    width: 300px;
    border: 1px solid rgba(0, 0, 0, 0.397);
    box-shadow: 3px 3px 6px grey;
    text-align: center;
    margin-top: 10px;
    margin-left: 11%;
}

.newItem img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    -webkit-transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
}

.newItem img:hover {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
