.photo {
    height: 152px;
}
.flexItm {
    width: 300px;
    border: 1px solid rgba(0, 0, 0, 0.397);
    box-shadow: 3px 3px 6px grey;
    text-align: center;
    margin: auto;   
    margin-top: 10px; 
}
.img {
    width: 20px;
    height: 20px;
    float: right;
    transition: filter 0.2s linear;
    filter: brightness(2) grayscale(0.3) opacity(0.7);
}
.img:hover {
    filter: none;
    cursor: pointer;
}
